/*!
 * Setting screen UI for ga-communicator.
 *
 * @deps dashicons, wp-codemirror, wp-components
 */.ga-heading span{vertical-align:middle;font-size:1em;width:1em;height:1em}.ga-error{color:#e24949;font-weight:bold}@keyframes ga-rotation{0%{transform:rotate(0deg)}50%{transform:rotate(270deg)}100%{transform:rotate(360deg)}}.ga-setting-row{position:relative}.ga-setting-row .dashicons{display:none}.ga-setting-row.loading .dashicons{color:#ddd;display:inline-block;animation:ga-rotation 1s ease-in-out infinite;vertical-align:middle}.ga-setting-row .notice{position:relative}.ga-setting-table{table-layout:auto;border-collapse:collapse;width:100%;margin:1em 0}.ga-setting-table th,.ga-setting-table td{border:1px solid #ddd;padding:0.5em}.ga-setting-example{padding:1em;font-size:small;background:#ddd;display:none}.ga-setting-example.toggle{display:block}#ga-setting-form .form-table{margin-bottom:40px}#ga-setting-form .form-table+h2{margin-top:0;padding-top:40px;border-top:1px solid #c3c4c7}#ga-toggle{margin-bottom:20px}#ga-toggle+table{display:none}#ga-toggle.table-open+table{display:table}.CodeMirror-line{padding-left:25px}.ga-sandbox-result{padding:1em;border:3px solid #000;background-color:#444 !important;color:#fff;font-family:monospace;box-sizing:border-box;width:100%;height:400px}.ga-sandbox-result::placeholder{color:#fff}.ga-sandbox-result.success{color:#2fb62f}.ga-sandbox-result.error{color:#ee4c4c}
